.App {
    gap: 1rem;
    padding: 1rem;
    background-color: #282c34;
    color: white;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
button {
    cursor: pointer;
    padding: 0.5rem;
    margin: 0 0.5rem;
    background: #282c34;
    color: white;
    border: 1px solid white;
    border-radius: 12px;
}
